.seriesSketch {
	width: 100%;
	display: flex;
	flex-direction: column;
	padding: 15px;
	box-sizing: border-box;
	border: 1px dashed #a0a0a0;
	border-radius: 5px;
}

.seriesSketch__image {
	width: 100%;
	border-radius: 5px;
}

.seriesSketch__title {
	margin: 10px 0;
	font-family: 'Quant Antiqua';
	font-style: normal;
	font-weight: 400;
	font-size: 10px;
	line-height: 130%;
	letter-spacing: 0.2em;
	text-transform: uppercase;
	color: var(--light-text-color);
}

.seriesSketch__discription {
	margin: 0;
	font-family: 'Quant Antiqua';
	font-style: italic;
	font-weight: 400;
	font-size: 12px;
	line-height: 130%;
	color: var(--simple-text-color);
}

@media screen and (min-width: 425px) {
	.seriesSketch {
		padding: 20px;
	}
}

@media screen and (min-width: 768px) {
	.seriesSketch__discription {
		font-size: 15px;
	}
}
